@import url(https://fonts.googleapis.com/css?family=Quicksand:500);@import url(https://cdnjs.cloudflare.com/ajax/libs/material-design-icons/3.0.1/iconfont/material-icons.min.css);
html, body {
  height: 100%;
  margin: 0;
}
html *, body * {
    font-family: 'Quicksand', sans-serif;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
#app {
  position: relative;
  top: 80px;
  padding: 15px;
  width: 100%;
  max-width: 600px;
  margin: auto;
  font-size: 16px;
}
.background--image {
  position: fixed;
  top: -50px;
  left: -50px;
  right: -50px;
  bottom: -50px;
  background-size: cover;
  background-position: center;
  background-color: #673AB7;
  background-image: url(/static/img/pexels-photo-1027393.7130f29.jpeg);
  -webkit-filter: blur(10px);
          filter: blur(10px);
  z-index: -1;
}
input {
  color: white;
  width: 100%;
  height: 60px;
  background: rgba(255, 255, 255, 0.2);
  font-size: 20px;
  padding: 0 20px;
  border: none;
  border-radius: 3px;
  outline: none;
  margin-bottom: 10px;
}
input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.7);
}
input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.7);
}
input::-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.7);
}
input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}
input::-moz-placeholder {
  color: white;
}
input:-moz-placeholder {
  color: white;
}
footer p {
  text-align: center;
  color: white;
  font-size: 13px;
}

.list--container {
  position: relative;
  padding: 15px;
  padding-bottom: 5px;
  border-radius: 3px;
  margin-bottom: 100px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
@media only screen and (max-width: 480px) {
.list--container {
      padding-left: 0;
      padding-right: 0;
}
}
ul {
  margin: 0;
  padding: 0;
}
.filter a {
  font-size: 13px;
  background: rgba(255, 255, 255, 0.4);
  color: white;
  padding: 6px 10px;
  display: inline-block;
  border-radius: 2px;
  margin-bottom: 10px;
  text-decoration: none;
  outline: none;
}
.filter .right {
  float: right;
}
@media only screen and (max-width: 480px) {
.filter .right {
      float: none;
}
}
.filter .right span {
    color: white;
    padding-left: 10px;
}
.filter--input {
  height: 50px;
  font-size: 1rem;
}
.selected {
  background: rgba(255, 255, 255, 0.2) !important;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}

.todo--item {
  position: relative;
  background-color: rgba(255, 255, 255, 0.2);
  list-style: none;
  font-size: 0.97rem;
  line-height: 20px;
  margin-bottom: 8px;
  padding: 20px 50px 20px 35px;
  border-radius: 3px;
  color: white;
  -webkit-box-shadow: 0 0 2px gray;
          box-shadow: 0 0 2px gray;
}
.todo--item:hover .list--options {
    display: block;
}
.completed {
  background-color: rgba(139, 195, 74, 0.8) !important;
  color: white !important;
}
.completed.todo--item {
    padding-left: 5px !important;
}
.completed.edit--input {
    background-color: #71a436 !important;
}
.list--options {
  position: absolute;
  right: 15px;
  top: 50%;
  margin-top: -10px;
  display: none;
}
.edit--input {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  font-size: 0.95rem;
  background: white;
  color: black;
}
.completed--icon, .edit--icon, .remove--icon {
  line-height: 20px;
  vertical-align: top;
  font-size: 20px;
  padding-left: 5px;
}
